.login {
    width: 100%;
    border: 2px solid black;
    height: 100vh;
    background: rgb(255, 0, 0);
    background: radial-gradient(circle, rgba(255, 0, 0, 1) 0%, rgba(0, 0, 0, 1) 100%);
}

.signin {
    width: 40%;
    margin-left: 10%;
    height: 80%;
    margin-top: 5%;
}



.signup {
    width: 40%;
    height: 80%;
    overflow: scroll;
    margin-top: 5%;
    transition: .4s;

}


.signup:hover {
    transform: scale(1.1);
    transition: .4s;
}

.signup::-webkit-scrollbar {
    display: none;
}

@media (max-width: 768px) {

    .signin,
    .signin img {
        display: none;
    }

    .signup {
        width: 400px;
    }

    .signup:hover {
        transform: scale(1);
        transition: .4s;
    }
}